@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria{
    padding: 5rem 0;
    h3{
        text-align:center ;
        font-weight: 700;
    }
}

.galeria-imagenes{
    @include m.resetear;
    @include m.grid(2);
    gap: 2rem;

    @include m.telefono{
        @include m.grid(3);
        gap: 2rem;
    }

    @include m.tablet{
        @include m.grid(4);
        gap: 2rem;
    }

    @include m.tablet{
        @include m.grid(4);
        gap: 2rem;
    }

    @include m.laptoXL{
        @include m.grid(6);
        gap: 2rem;
    }

}

.overlay
{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;
  background-color: rgba((0), 0, 0, .5);

  picture{
    display: block;
    max-width: 80rem;
  }
}


.Btn-cerrar{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover{
        cursor: pointer;
    }

   
}