@use 'base/variables' as v;
@use 'base/mixins' as m;


.header{

    background-color: v.$verde;

    @include m.tablet
    {
      &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: .3rem .3rem .3rem #333;
 
     }  
    }

    

    h1{
        color: v.$blanco;
        text-align: center;
        margin: 0;
    }
     


    .contenido-header{

      padding: 2rem; // le hace bajar por asi decirlo a los textos

       @include m.tablet{
          display: flex; // crea 2 columnas automaticamente.
          justify-content: space-between; // para colocar uno del lado izquierdo y otro del lado derecho
          align-items: center;

       }
    }
}

.navegacion-principal
{

  @include m.tablet
  {
    display: flex;  // los pone ordenados 
    column-gap: 2rem;
  }


  a{
    display: block;  //para colocar de arriba hacia abajo
    text-align: center;
    color: v.$blanco;
    margin-bottom: 1rem; // espacios entre los enlaces
    font-size: 2.4rem;


    @include m.tablet{
      margin: 0%;
    }


     &:hover{
       color: v.$amarillo; // hace un efecto al pasar el raton sobre el texto y cambia el color
     }

  }
}