@use 'base/mixins' as m;
@use 'base/variables' as v;

.Lineup{
  background-color: v.$Rosa;
  padding: 5rem 0;


  h3,p{
    text-align: center;
    color:v.$blanco;
  }

  .dia{
    text-transform: uppercase; // a mayuscula.
    margin: 5rem 0;
  }


}

.escenarios-contenedor {


    @include m.tablet{
        @include m.grid(2);
    }

    p{
        color :v.$negro;
    }

    .escenario{
        padding-top: 2rem;
    }

    
}
.icono{
    content: '';
    display: block;
    width: 20rem;
    height: 10rem;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 2rem auto 0 auto;


}

.icono-rock{
    content: '';
    display: block;
    width: 20rem;
    height: 10rem;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 2rem auto 0 auto;


}


.calendario{
    @include m.resetear;
    font-weight: 700;
    li{
        padding: 2.5rem;

        span{
            font-weight: 400;
        }

    }
}



.bg-amarillo{
    background-color: v.$amarillo;
    padding-top: 5rem;

    li:nth-child(odd)
    {
      background-color: darken(v.$amarillo,10%);
    }

}


.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd)
    {
      background-color: darken(v.$verde,10%);
    }
}


