@use 'variables' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*::before,
*::after{
    box-sizing: inherit; //permite que todos los elementos se adepaten a su contenedor y que se excluya el relleno
}

body{
    font-family: v.$fuente_principal ;  // todos los elementos de el cuerpo van a tener la misma letra.
    font-size: 1.6rem;
    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 8.6rem;
    }
}

p{
    font-size: 2rem;
    color : v.$negro;
    line-height: 3rem;
}

.contenedor{
    width: 95%;
    max-width: 120rem;    
    margin: 0 auto;   //centrar horizontalmente 
}

a{
    text-decoration: none; // quitar esa linea de los enlaces
}

h1,h2,h3{
    margin: 0 0 5rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    display: block;
}





