@use 'base/mixins' as m;
@use 'base/variables' as v;

.video{
        position: relative;
        max-height: 70rem;
        overflow: hidden;
        min-height: 50rem;
        
    .overlay{
        position: absolute;
        background: rgb(125,2,195);
        background: linear-gradient(270deg, rgba(126,2,195,0.5) 0%, rgba(255,0,110,3) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    video{
     
        
        width: auto;
        display: block; // toma todo el espaci

        @include m.tablet
        {
            width: 100%;
        }
    }
}

.contenido-video
{
  h2
  {
     color:v.$blanco;
     font-size: 7rem;
     margin: 0; 
  }
  p{
     background-color: v.$Rosa;
     display: inline-block;
     padding: 1rem 2rem;
     color: v.$blanco;
     font-weight: 700;

  }
}