@use 'base/mixins' as m;
@use 'base/variables' as v;


.boletos{
    padding: 5rem 0;

    h3{
        text-align: center;
        color:v.$blanco;
        margin: 0;
    }
    .contenedor-boletos{
        @include m.tablet{
        @include m.grid(2);
        column-gap: 2rem;

        }
        
    }
}



.pase{

   .pase-header{
     background-color: v.$Rosa;
     padding: 0.5rem 0;
     text-transform: uppercase;
     p{
        text-align: center;
        color: v.$blanco;
        margin: 0;
        font-size: 2.4rem;
        font-weight: 700;

     }
   }

   .pase-body{
    background-color: v.$blanco;

    ul{
        @include m.resetear;
        li,&:nth-child(odd){
            background-color: v.$blanco;
            padding: 1rem;
            border-bottom: 1px solid #e1e1e1;
        }
    }

   }

   .pase-footer{
    background-color: v.$morado;

    p{
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        font-weight: 700;
        margin: 0;
        
     }
     text-transform: uppercase;
   }


}


.footer{
    background-color: v.$Rosa;
    padding: .5rem 0;

    p{
        text-align: center;
        color : v.$blanco;
        margin: 0;
        
        
    }
}

.premium ,.basico {
    p{
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        font-weight: 700;
    }
}

