@use 'variables' as v;


@mixin telefono{

    @media (min-width:480px) 
    {
        @content;    
    }
}

@mixin tablet{

    @media (min-width:768px) 
    {
        @content;     
    }
}

@mixin lapto
{
    @media (min-width:992px) 
    {
      @content;    
    }


}

@mixin laptoXL
{
    @media (min-width:1200px) 
    {
      @content;    
    }
}

@mixin grid($columnas){
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    column-gap: 5rem;

}

@mixin resetear{
    margin: 0;
    padding:0;
    list-style: none;
    
}




